const Noticia = ({noticia}) => {
    const {url, urlToImage, title, description, source, author, publishedAt} = noticia;

    let imagen ='';

    if(urlToImage !== ''){
        imagen = <img src={urlToImage} alt={title} className="card-img-top" />
    }else{
        imagen = <p className="text-center my-5">Imagen no encontrada</p>
    }
    return ( 
        <div className="col-md-6 col-12 mb-4">
            <div className="card">
                <div className="card-image">
                    {imagen}
                </div>
            </div>
        </div>
    );
}
 
export default Noticia;